import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { CustomValidators } from 'ng2-validation';

import { ValidationModel } from './../../models/validation.class';
import { Location } from '@angular/common';
import { Validations } from '../../../validations';
import { UserService } from '../../services/userService';
import { ShowToast } from '../../../showtoast';
import { AmChartsDirective } from '@amcharts/amcharts3-angular';
import { GetChemistDataService } from '../../services/GetChemistDataService';
import { AddAreaService } from '../../services/AddAreaService';

declare var jQuery: any;



@Component({
  selector: 'profile',
  templateUrl: './profile.component.html',
  styleUrls: [
    './profile.component.scss',
    '../../../../assets/icon/icofont/css/icofont.scss'
  ],
  providers: [UserService, GetChemistDataService, AddAreaService]
})
export class ProfileComponent implements OnInit {

  public enterOldPassword: boolean = false;
  public enterNewPassword: boolean = false;
  public enterConfirmPassword: boolean = false;

  public oldPassword: any;
  public newPassword: any;
  public confirmPassword: any;

  public session_obj: any;
  public showLoader: boolean = false;
  public showAction: boolean = true;

  public validationClass: Validations;

  public ShowToast: ShowToast;
  public cities_array = [];
  public areas_array = [];

  public editingSaveBtn: boolean = false;

  public gotData: boolean = false;

  public ProfilePicture: File;

  public emptyName: boolean = false;
  public emptyContactPerson: boolean = false;
  public emptyLatitude = false;
  public emptyLongitude = false;
  public emptyImage = false;

  public Validations: Validations;


  public LogoUrl: any;

  constructor(public _userService: UserService,
    public _getChemistDataService: GetChemistDataService,
    public _AddAreaNameService: AddAreaService,
  ) {
    this.validationClass = new Validations();
    this.ShowToast = new ShowToast();
    this.Validations = new Validations();
  }




  getCityAreasByThisCity(elem){
      this.session_obj.Chemist.CityId = elem;
      this.getCityAreas();
  }




  changeImage() {
    $("#imgInp").click();
  }

  changeimage(elem) {
    this.readURL(elem);
  }
  readURL(input) {


    var reader: any,
      target: EventTarget;
    reader = new FileReader();

    input = document.getElementById('imgInp');
    if (input.files && input.files[0]) {
      // var reader = new FileReader();
      var self = this;
      reader.onload = function (e) {
        console.log(e);
        $('#blah').attr('src', e.target.result);
        self.ProfilePicture = input.files[0];
        $("#blah").css("width", "50px");
        self.saveImageService();
      }

      reader.readAsDataURL(input.files[0]);


    }
  }


  saveImageService() {

    this._userService.saveImageForPharmacy(this.ProfilePicture).subscribe(a => {
      this.LogoUrl = a.data;
    })
  }

  getAllCities() {
    this._getChemistDataService.getAllCitiesService().subscribe(
      response => {
        this.cities_array = response.data;
        console.log(this.cities_array);
      });
  }// End of Get All Cities
  getCityAreas() {

    this._AddAreaNameService.getAreaName(this.session_obj.Chemist.CityId).subscribe(
      response => {
        this.areas_array = response.data;
        console.log(this.areas_array);
      });


  }// End of Get Areas of City

  resetErrorFields() {
    this.enterOldPassword = false;
    this.enterNewPassword = false;
    this.enterConfirmPassword = false;
  }


  SavePasswordWithEnter(event) {
    if (event.keyCode == 13) {
      this.savePassword();
    }
  }

  unsetValues() {
    this.oldPassword = undefined;
    this.newPassword = undefined;
    this.confirmPassword = undefined;
  }


  resetEditErrors() {
    this.emptyName = false;
    this.emptyContactPerson = false;
    this.emptyLatitude = false;
    this.emptyLongitude = false;
    this.emptyImage  = false;

  }

  EditPharmacyService() {
    var error = false;

    this.resetEditErrors();

    if (this.Validations.verifyNameInputs(this.session_obj.Chemist.PharmacyName)) {
      error = true;
      this.emptyName = true;
    }
    if (this.Validations.verifyNameInputs(this.session_obj.Chemist.ContactPersonName)) {
      error = true;
      this.emptyContactPerson = true;
    }

    if (this.Validations.verifyNameInputs(this.session_obj.Chemist.Latitude)) {
      error = true;
      this.emptyLatitude = true;
    }

    if (this.Validations.verifyNameInputs(this.session_obj.Chemist.Longitude)) {
      error = true;
      this.emptyLongitude = true;
    }

    if(this.LogoUrl == undefined || this.LogoUrl == null){
      error = true;
      this.emptyImage= true;
    }


  }

  savePassword() {

    this.resetErrorFields();
    var error = false;

    if (!this.validationClass.verifyNameInputs(this.oldPassword)) {
      error = true;
      this.enterOldPassword = true;
    }

    if (!this.validationClass.verifyNameInputs(this.newPassword)) {
      error = true;
      this.enterNewPassword = true;
    }

    if (!this.validationClass.verifyNameInputs(this.confirmPassword)) {
      error = true;
      this.enterConfirmPassword = true;
    }


    if (this.newPassword != this.confirmPassword) {
      error = true;
      this.enterConfirmPassword = true;
    }



    if (error) {

    } else {

      this.showLoader = true;
      this.showAction = false;


      this._userService.changePassword(this.oldPassword, this.newPassword).subscribe(a => {
        console.log(a);

        if (a.code == 200) {

          this.showLoader = false;
          this.showAction = true;

          this.unsetValues();

          $("#snackbar").html(a.message);
          this.ShowToast.myFunction();

        } else {


          this.showLoader = false;
          this.showAction = true;

          this.unsetValues();

          $("#snackbar").html(a.message);
          this.ShowToast.myFunction();

        }

      }, err => {
        console.log(err);
        var obj = JSON.parse(err._body);
        $("#snackbar").html(obj.message);
        this.ShowToast.myFunction();
      })

    }


  }

  makeEditable() {
    this.editingSaveBtn = true;
    jQuery(".editable").prop("readonly", false);
    jQuery(".editable-select").prop("disabled", false);
  }

  ngOnInit(): void {

    this.session_obj = JSON.parse(localStorage.getItem('session_obj'));

    if(this.session_obj == undefined || this.session_obj == null){
      this.getAllCities();
      this.getCityAreas();
     
    }
   

    this.gotData = true;



  }

}




